<template lang="pug">
  .mobile-nav(:class="classNav")
    button(type='button' :class="classClose" @click="$emit('close')")._close
      icon(name="popup-close" component="popup")._close-ico
    ._header
      img(src=`${process.env.MIX_IMG_PATH}/header/logo.png`, alt="Автолидер и партнеры")._logo
      ._title Автолидер и партнеры
    ._content
      ._group
        ._list
          router-link(:to="{ name: link.name }" v-for="(link, index) in links" :key="index")._link
            icon(:name="link.ico" component="nav")._ico
            | {{ link.title }}

        ._contacts
          ._item
            icon(name="call" component="header")._contact-ico
            a.mobile-nav__phone(:href="`tel:${phone}`" v-for="(phone, index) in contacts.phone" :key="index") {{ phone }}
          ._item
            icon(name="time" component="header")._contact-ico
            ._text(v-for="(open, index) in contacts.open" :key="index") {{ open }}

          ._item
            icon(name="place" component="nav")._contact-ico
            ._text {{ contacts.address }}

</template>
<script>
export default {
  name: "Mobile-nav",
  props: {
    links: {
      type: Array,
      required: true
    },
    contacts: {
      type: Object,
      default: () => {
        return {};
      }
    },
    showNav: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    classNav() {
      return {
        "mobile-nav_active": this.showNav
      };
    },
    classClose() {
      return {
        "mobile-nav__close_active": this.showNav
      };
    }
  },
  watch: {
    showNav() {
      this.showNav
        ? document.body.classList.add("open-modal")
        : document.body.classList.remove("open-modal");
    }
  }
};
</script>
<style lang="scss">
